body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    -webkit-animation: App-logo-spin infinite 20s linear;
            animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

.contenedor-calculadora {
  width: 400px;
  height: 600px;
  padding: 14px;
  background-color: #d8e0e9;
  border-radius: 20px;
  border: 5px solid white;
  position:absolute;
  top: 50%;
  left: 50%;
  -webkit-transform: translate(-50%, -50%);
          transform: translate(-50%, -50%);
  box-shadow: 1px 3px 8px 1px rgb(122, 120, 120);
  /* margin: -25% 0 0 -25%; */
}

.fila {
  margin: 2px;
  display: flex;
  align-items: center;
  justify-content: center;
}



@-webkit-keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}



@keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

.boton-contenedor {
    height: 83px;
    display: flex;
    flex: 1 1;
    justify-content: center;
    align-items: center;
    font-weight: normal;
    font-size: 38px;
    background: linear-gradient(to bottom right, rgb(69, 69, 71), rgb(110, 107, 107));
    color: white;
    border-radius: 10px;
    border: 2px solid rgb(77, 75, 75);
    margin: 2px;
    cursor: pointer;
    -webkit-user-select: none;
            user-select: none;
  }
  
  .boton-contenedor:hover {
    background: linear-gradient(to bottom right, rgb(110, 107, 107), rgb(69, 69, 71));
    /* background-color: #5a01a7; */
  }
  
  .operador {
    background-color: #00471b;
  }
  
  .operador:hover {
    background: linear-gradient(to bottom right, #ff6600, #ff6600);
    background-color: #ff6600;
  }
.boton-clear {
    height: 80px;
    font-size: 1.6em;
    display: flex;
    flex: 1 1;
    background-color: #ff7b00;
    margin-top: 8px;
    justify-content: center;
    align-items: center;
    font-weight: bold;
    color: white;
    border: 2px solid white;
    border-radius: 45px;
    cursor: pointer;      
  }
  
  .boton-clear:hover {
    background-color: #ebb000;
  }
.input {
    height: 50px;
    border-radius: 10px;
    margin-bottom: 20px;
    display: flex;
    justify-content: flex-end;
    align-items: center;
    font-weight: bold;
    font-size: 30px;
    /* background: linear-gradient(to bottom right, rgb(71, 177, 80), rgb(57, 119, 57)); */
    background-color: #0a0a23;
    color: white;
    padding: 11px 30px 11px 11px;
    margin: 1px solid #888;
    border: #bdbdbd;
    box-shadow: 1px 3px 8px 1px rgb(122, 120, 120);
  }
